import React from "react";

const Notification = ({ message }) => {
  const styles = {
    color: "green",
    background: "lightgrey",
    fontSize: 20,
    borderStyle: "solid",
    borderRadius: 5,
    padding: 10,
    margin: "auto",
    width: "80vw",
  };

  return message === null ? null : <div style={styles}>{message}</div>;
};

export default Notification;
