import React, { useState, useEffect } from "react";
import SearchFilter from "./components/SearchFilter";
import AddContactForm from "./components/AddContactForm";
import ShowContacts from "./components/ShowContacts";
import contactService from "./services/contacts";
import Notification from "./components/Notification";

const App = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState("");
  const [newNumber, setNewNumber] = useState("");
  const [filterText, setFilterText] = useState("");
  const [message, setMesssage] = useState(null);

  useEffect(() => {
    contactService.getAll().then((initialContacts) => {
      setPersons(initialContacts);
    });
  }, []);

  const onFormSubmit = (event) => {
    console.log("cia numberis", newNumber);
    event.preventDefault();
    const newContact = {
      name: newName,
      number: newNumber,
    };

    const updateNumber = (findPers) => {
      const updatedNumber = { ...findPers, number: newNumber };
      contactService
        .update(findPers.id, updatedNumber)
        .then((returnedContact) => {
          setPersons(
            persons.map((person) =>
              person.id !== findPers.id ? person : returnedContact
            )
          );
          setMesssage("Number updated.");
          setTimeout(() => {
            setMesssage(null);
          }, 3000);
        })
        .catch((error) => {
          alert("Error Alert!", error);
        });
    };
    const findPers = persons.find(({ name }) => name === newName);
    findPers
      ? (window.confirm(
          `${newName} - exists in your contacts. Do you want to update number?`
        ) &&
          updateNumber(findPers)) ||
        setNewName("") ||
        setNewNumber("")
      : newNumber === "" || newName === ""
      ? alert(`Check your fields.`)
      : (contactService
          .create(newContact)
          .then((returnedContact) => {
            setPersons(persons.concat(returnedContact));
            setMesssage("New Contact is added.");
            setTimeout(() => {
              setMesssage(null);
            }, 3000);
          })
          .catch((error) => {
            console.log("Cia erroras", error.response.data);
            setMesssage(
              `This is my error from my server: ${error.response.data.err}`
            );
            setTimeout(() => {
              setMesssage(null);
            }, 3000);
          }) &&
          setNewName("")) ||
        setNewNumber("");
  };

  const handelNameChange = (event) => {
    setNewName(event.target.value);
  };
  const handelNumberChange = (event) => {
    setNewNumber(event.target.value);
  };
  const handelFilterTextChange = (event) => {
    setFilterText(event.target.value);
  };
  const handelBtnClick = (id) => {
    if (window.confirm("Do you realy want to delete?")) {
      contactService.deleteContact(id).then(() => {
        setPersons(persons.filter((person) => person.id !== id));
      });
    }
  };
  return (
    <div>
      <Notification message={message} />
      <h2>Phonebook</h2>
      <SearchFilter
        filterText={filterText}
        handelFilter={handelFilterTextChange}
      />

      <h2>Names and Numbers</h2>

      <ShowContacts
        persons={persons}
        filterText={filterText}
        handelBtnClick={handelBtnClick}
      />

      {/* {filterText === ""
        ? persons.map((person, idx) => (
            <PersonData name={person.name} number={person.number} idx={idx} />
          ))
        : persons
            .filter(
              (person) =>
                person.name.toLowerCase().indexOf(filterText.toLowerCase()) !==
                -1
            )
            .map((person, idx) => (
              <PersonData name={person.name} number={person.number} idx={idx} />
            ))} */}
      {/* {filterText === ""
        ? persons.map((person, idx) => (
            <p key={idx}>
              {person.name} -> {person.number}
            </p>
          ))
        : persons

            .filter(
              (person) =>
                person.name.toLowerCase().indexOf(filterText.toLowerCase()) !==
                -1
            )
            .map((person, idx) => (
              <p key={idx}>
                {person.name} -> {person.number}
              </p>
            ))} */}
      <hr />
      <AddContactForm
        onFormSubmit={onFormSubmit}
        newName={newName}
        newNumber={newNumber}
        handelNameChange={handelNameChange}
        handelNumberChange={handelNumberChange}
      />
    </div>
  );
};

export default App;
