import React from "react";
import PersonData from "./PersonData";

const ShowContacts = ({ persons, filterText, handelBtnClick }) => {
  return (
    <div>
      {filterText === ""
        ? persons.map((person, idx) => (
            <PersonData
              name={person.name}
              number={person.number}
              key={idx}
              handelBtnClick={() => handelBtnClick(person.id)}
            />
          ))
        : persons
            .filter(
              (person) =>
                person.name.toLowerCase().indexOf(filterText.toLowerCase()) !==
                -1
            )
            .map((person, idx) => (
              <PersonData name={person.name} number={person.number} key={idx} />
            ))}
    </div>
  );
};

export default ShowContacts;
